@mixin a-link {
  color: $theme-color;
  text-decoration: none;
}

@mixin nav-margin {
  padding: 0px;
  margin-block-end: 2em;
}

//color properties
$accent-color: #F53A2B;
$theme-color: black;
$background-color: white;

html {
  background-color: $background-color;
  color: $theme-color;
  font-size: 100%;
}

.top-divider {
  width: 100%;
  border-top: 1px $theme-color solid;
  position: absolute;
  z-index: 300;
  top: 24px;
}

.header {
  position: relative;
  z-index: 200;
  margin-top: 24px;
  margin-bottom: 16px;
}

.header > div > h1 {
  margin-block-start: 16px;
  font-weight: 400;
  font-size: 1.8em;
  max-width: 300px;
  letter-spacing: -0.1rem;
}

.header > div > h1 > a {
  @include a-link();
}

.eng-name {
  color: $accent-color;
}

.nav {
  @include nav-margin();
  // position: fixed;
  margin-block-start: 0em;
}

.contact {
  @include nav-margin();
}

a > li {
  letter-spacing: -0.04rem;
  margin-bottom: 0.5em;
  list-style-type: none;
}

.nav > a {
  @include a-link();
  font-size: 1.3rem;
}

.contact > a {
  @include a-link();
  font-size: 1.1rem;
}

.contact > a:hover {
  color: $accent-color;
}

.sidebar {
  margin-bottom: 24px;
  z-index: 199;
}

.chinese-name {
  font-size: 2em;
  font-weight: 900;
  color: $accent-color;
}

.content {
  z-index: 100;
}

.gallery-card {
  margin-block-end: 2em;
}

.gallery-card-img > img {
  object-fit: cover;
  width: 100%;
  min-height: 30vh;
  margin-block-end: 1em;
}

.description {
  // padding: 0 0.8em;
  min-height: 2rem;
  font-size: 1rem;
}

.description-title {
  font-weight: 400;
  font-size: 1.4rem;
  margin-block-start: 0.1em;
  margin-block-end: 0.4em;
}

.description-tags {
  display: block;
}

.tag {
  font-size: 0.9rem;
  color: $accent-color;
  display: inline-block;
  padding: 4px;
  border: 1px solid;
}

.description-text {
  margin-block-start: 1.2em;
  line-height: 1.3em;
}

.btn {
  display: inline-block;
  margin-block-start: 1em;
}

.btn:hover {
  color: $accent-color;
} 

.description > .divider {
  margin-top: 1em;
  opacity: 50%;
}

// exploration 2
a {
  text-decoration: none;
  color: black;
}

a:hover {
  -webkit-text-fill-color: $accent-color;
  -webkit-text-stroke-color: $accent-color;
}

.works {
  margin-block-end: 2rem;
}

.works-name {
  font-size: 5rem;
  font-weight: 900;
  // text-transform: lowercase;
  letter-spacing: -0.3rem;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 1.3px;
  -webkit-text-stroke-color:$theme-color;
  line-height: 3.3rem;

}


$work-img-width: 45rem;
$work-img-height: 45rem;

.works-img > img {
  position: fixed;
  top: 50%;
  left: 50%;
  object-fit: cover;
  width: $work-img-width;
  height: $work-img-height;
  margin-top: -($work-img-height/2);
  margin-left: -($work-img-width/2);
  z-index: -200;
  // opacity: 80%;
}

.nav-2 {
  margin-block-start: 16px;
  text-align: right;
  mix-blend-mode: difference;
}

// fixed sidebar for screens larger than iPad
@media only screen and (min-width: 768px) {
  .btm-nav {
    position: fixed;
    bottom: 24px;
  }
}

// resize the font for different size of screens 
@media only screen and (max-width: 375px) {
  html {
    font-size: 80%;
  }
}

@media only screen and (min-width: 1920px) {
  html {
    font-size: 125%;
  }
}

@media only screen and (min-width: 2560px) {
  html {
    font-size: 180%;
  }
}
